export default {
	data() {
		return {
			requestParams:{},
			entry:{}
		}
	},
	onLoad(e) {
		this.requestParams=e;
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
		var params=new Object();
		params.id=this.requestParams.articleid;
		let list = [{}];
		var that=this;
		this.$api.ADInfo(params,function(data){
			if(data.success){
				that.entry=data.data;
			}else{
				that.$common.errorToShow(data.message)
			}
			
		})
	},
	methods: {
		
	}
}
